@import "../_cms-variables";
@import "../_cms-mixins";

.cmp-product-slide(@width){
	background: #fff;
	.swiper{
		position: relative;
		.size(100%);

		.swiper-slide{
			float: left;
			overflow: hidden;
			transform-style: preserve-3d;
			width:unit(@width,%);
		}
	}
	.swiper-pagination{
		.absolute(_,0,0.3rem,_);
		z-index: 5;
		height: .16rem;
		width: 100%;
		text-align: center;

		> .swiper-pagination-bullet{
			display: inline-block;
			vertical-align: top;
			text-align: center;
			.size(.18rem);
			margin-right: .3rem;
			border-radius: 100%;
			background: @border-color-base;
			border: .04rem solid @border-color-base;

			&-active{
				background: @ht-color;
				border: .02rem solid @ht-color;
			}
		}

	}
	.tlt{
		padding:0 10%;
		.ellipsis(2);
		height:@font-size-base * @line-height-base * 2;
		overflow: hidden;
		color:@text-color;
		font-weight: normal;
	}
	.pic{
		width:80%;
		margin:5% auto;
		overflow: hidden;
	}
	.price{
		margin:5% 10%; 
		height: 20px;
		overflow: hidden;

		del{
			color: #999;
			width: 1.2rem;
			padding-left: 5px;
			font-size: @font-size-sm;
			.ellipsis();
		}
	}
}
