@import "../mixins";
@import "../variables";

.cmp-top-floor(@z-index: @zindex-fixed, @nav-size: 1.76rem, @panel-active: 'panel-activated',@theme-color:@ht-color) {
	line-height: @nav-size;
	height: @nav-size;

	.bg{
		.fixed(0,0,0,0);
		opacity: 0;
		z-index: -1;
		transition: opacity .3s ease;
		transform: translateZ(0);
		background-color: rgba(0,0,0,.6);
	}

	.item-list{
		z-index: @z-index;
		max-width: @container-max-width;
		top: 0;
		right: 0;
		left: 0;
		height: @nav-size;
		background: #fff;
		margin: 0 auto;
		transform: translateZ(0);

		.navs{
			margin-right: @nav-size;
			height: 100%;
			position: relative;

			.list{
				a{
					padding: 0 10px;
					display: block;
					width: auto;
					position: relative;

					&:before{
						content: '';
						.absolute(_,_,0,0);
						.size(100%, 2px);
						background: @theme-color;
						display: none;
					}

					&.active{
						color: @theme-color;

						&:before{
							display: block;
						}
					}
				}
			}

			.type{
				padding: 0 10px;
				display: none;
				background: #fff;
				.absolute(0,_,_,0);
				width: 100%;
			}

			&-nr{
				margin-right:0;
			}
		}

		.toggle-btn{
			float: right;
			.size(@nav-size, 100%);
			position: relative;
			transition: transform .3s ease;

			&:before,&:after{
				content: '';
				.absolute(50%,_,_,_);
				.size(30%, 5%);
				background-color: #6c6c6c;
				transform-origin: 50% 50%;
			}

			&:before{
				left: 26%;
				transform: rotate(45deg);
			}

			&:after{
				right: 26%;
				transform: rotate(-45deg);
			}
		}
	}

	.sub-list{
		transition: transform .3s ease;
		transform: translateY(-100%);
		z-index: -1;
		.absolute(0, 0, _, 0);
		max-width: @container-max-width;
		background: rgba(255,255,255,0.9);
		min-height: @nav-size;

		li{
			float: left;
			width: 25%;
			text-align: center;
			padding: 0 10px;

			&.active a{
				color: @theme-color;
			}
		}
	}

	&.fixed, &.panel-activated{
		.item-list{
			position: fixed;
		}
	}

	&.panel-activated{
		.item-list .toggle-btn{
			transform: rotate(180deg);
		}

		.bg{
			z-index: @z-index;
			opacity: .7;
		}

		.sub-list{
			z-index: @z-index;
			transform: translateY(0);
			.fixed(@nav-size, 0, _, _);
			margin:0 auto;
		}

		.item-list{
			.navs{
				.list{
					visibility: hidden;
				}

				.type{
					display: block;
				}
			}
		}
	}
}
