@import "../../_plugin/cmp-product-slide.less";

@import "../_promotion-variables.less";
@import "../_promotion-mixins.less";


.fn-@{cms-ns}-low-price{
	
	.cmp-product-slide(33.3);

	.swiper{
		.swiper-slide{
		    border-right: 1px solid @border-color-base;

		    .more{
		    	display: block;
		    	text-align:center;
		    	
		    	 &:before{
		    		content: '';
		    		display: block;
		    		.size(100%);
		    		padding-top: 100%;
		    		padding-bottom: 60px;
		    	}

		    	.absolute{
		    		.absolute(0,_,_,0);
		    		.size(100%);
		    		margin-top: 50%;

		    		> * {
			    		display: block;
			    	}	
		    	}

		    	

		    	.text{
					line-height: 1.2;	
		    	}

		    	.hd-more-icon{
		    		
					background: @ht-color;
					width:20px;
					border-radius:3px;
					color:#fff;
					position: relative;
					margin: 5px auto 0;

					&:before,&:after{
						content: '';
						position: absolute;
						top: 40%;
						width: 30%;
						background-color: #fff;
						height: 5%;
						transform-origin: 50% 50%;
					}

					&:before{
					    left: 35%;
						transform: rotate(45deg);
					}
					&:after{
					    right: 35%;
					    top:60%;
						transform: rotate(-45deg);
					}
		    	}
		    }
		}
	}	
}