@import "../_cms-variables";
@import "../_cms-mixins";

.cmp-item-list(){
	.item{
		width:49.3%;
		float: left;
		background: #fff;
		margin-top: 1.4%;
		overflow: hidden;
		position: relative;

		&-wrap{
			.absolute(0,_,_,0);
			.size(100%);
		}

		&:nth-child(even){
			float: right;
		}

		.img-box{
			display: block;
		}

		.title{
			display: block;
			color: #333;
			height:unit(@line-height-base * @font-size-base * 2,rem);
			.ellipsis(2);
			width: 90%;
			margin:.4rem auto;
			overflow: hidden;
		}

		.price{
			.size(90%,20px);
			margin:0 auto .2rem auto;
			overflow: hidden;
			line-height: @line-height-computed-lg;

			.price-left{
				width: 57%;
				float: left;
				.ellipsis();
			}

			.price-wrap{
				max-width: 60%;
				.ellipsis();
			}

			.now{
				font-weight: 400;
				//max-width:50%;
				overflow: hidden;
				.ellipsis();
				margin-right:3px;
				float: left;
			}
			del{
				color: #999;
				//max-width: 38%;
				.ellipsis();
				float: left;
				font-size:@font-size-sm;
				line-height: 2.1;
				height:@line-height-computed-lg;
				overflow: hidden;
			}
			.add-btn{
				color:@price-color;
				font-size:@font-size-lg;
				float: right;
			}
			.text{
				font-size:@font-size-sm ;
				color:#999;
				float: right;
				line-height: 1.8;
			}
		}

		&-img{
			.item-wrap{
				padding-top:50%;
				padding-bottom:3.5rem;

				.img100{
					.absolute(0,_,_,0);
					.size(100%);
				}
			}
		}

	}
}
