@import "../_cms-variables";
@import "../_cms-mixins";

.cmp-common-title(@txt-color,@line-color){
	line-height: 3;
	color: @txt-color;
	text-align: center;
	span{
		padding:0 10px;
		position: relative;

		&:before,&:after{
			display: block;
			content:'';
			.absolute(50%,_,_,_);
			.size(60%,1px);
			background:  @line-color;
		}
		&:before{
			left:-60%;
		}
		&:after{
			right:-60%;
		}
	}
}


.cmp-common-hd(@theme-color){
	overflow: hidden;
	position: relative;
	border-left:4px @theme-color solid;
	background: #f9f9f9;
	padding:10px;
	color:@theme-color;

	.hd-txt{
		float: left;
		margin-right:10px;
	}
	.hd-detail{
		float: left;
		color:@theme-color;
	}
	.hd-limit-time{
		width:200px;
		text-align: right;
		float: right;
		font-size: @font-size-sm;
		margin-top: 2px;
		
		.word{
			padding-right:10px;
		}
		.num{
			background: #000;
			color:#fff;
			padding:1%;
			border-radius:3px;
		}
	}
	.hd-more{
		float: right;
		width:20%;
		text-align: right;
		&-icon{
			display: inline-block;
			background: @theme-color;
			width:20px;
			border-radius:3px;
			color:#fff;
			position: relative;
			margin-left:8%;

			&:before,&:after{
				content: '';
				position: absolute;
				top: 40%;
				width: 30%;
				background-color: #fff;
				height: 5%;
				transform-origin: 50% 50%;
			}

			&:before{
			    left: 35%;
				transform: rotate(45deg);
			}
			&:after{
			    right: 35%;
			    top:60%;
				transform: rotate(-45deg);
			}
		}
	}
}
